<template>
  <div class="app-wapper">
    <v-header v-show="showTitle"  class="v-header" @touchmove.native.prevent></v-header>
    <consume-ok-popup></consume-ok-popup>
    <receive-ok-popup></receive-ok-popup>
    <!-- exclude为不缓存的组件，注意是组件名，不是路由名称 -->
    <keep-alive exclude='v-details,pay-affirm,pledge-affirm,agency-shops,userlist,user-buyed,ticket-list,refresh-page,store-buyed,income-expense,bind-name,bind-bank,bank-list,get-money,shop-oreder-home'>
      <router-view class="router-view"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import vHeader from "./components/header/header.vue";
import consumeOkPopup from "./components/popupBox/consumeOkPopup.vue";
import receiveOkPopup from "./components/popupBox/receiveOkPopup.vue";
import sharejs from "./common/js/sharejs.js";
import { mapState } from "vuex";
export default {
  name: "app",
  components: {
    vHeader,
    consumeOkPopup,
    receiveOkPopup
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["showTitle"])
  },
  created() {},
  activated() {},
  methods: {}
};
</script>

<style lang="stylus">
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.25s;
}

.fade-enter, .fade-leave-to { /* .fade-leave-active below version 2.1.8 */
  opacity: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif, 'Microsoft YaHei';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}

.app-wapper {
  width: 100%;
  height: 100%;

  .router-view {
    height: 100%;
    box-sizing: border-box;
  }
}
</style>


